.userPhoto {
    width: 100px;
    height: 100px;
    border-radius: 50%;

}

.userItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px;
    max-width: 600px;
    margin: 30px;
    border: 2px solid #efefef;
    background: #e0e5ec;
}

.btnUnfollow {
    width: 130px;
    height: 40px;
    border-radius: 5px;
    padding: 10px 25px;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
    7px 7px 20px 0px rgba(0,0,0,.1),
    4px 4px 5px 0px rgba(0,0,0,.1);
    outline: none;
    border: none;
    color: #000;
}

.btnUnfollow:after {
    position: absolute;
    content: "";
    width: 0;
    height: 100%;
    top: 0;
    left: 0;
    direction: rtl;
    z-index: -1;
    box-shadow:
            -7px -7px 20px 0px #fff9,
            -4px -4px 5px 0px #fff9,
            7px 7px 20px 0px #0002,
            4px 4px 5px 0px #0001;
    transition: all 0.3s ease;
}
.btnUnfollow:hover {
    color: #000;
}
.btnUnfollow:hover:after {
    left: auto;
    right: 0;
    width: 100%;
}
.btnUnfollow:active {
    top: 2px;
}

.userAva {
    display: flex;
    flex-direction: column;
    align-items: center;
}


